// Îí§Ïóê as const Î•º Î∂ôÏó¨Ï§åÏúºÎ°úÏç® ÎÇòÏ§ëÏóê Ïï°ÏÖò Í∞ùÏ≤¥Î•º ÎßåÎì§Í≤å action.type Ïùò Í∞íÏùÑ Ï∂îÎ°†ÌïòÎäî Í≥ºÏ†ïÏóêÏÑú
// action.type Ïù¥ string ÏúºÎ°ú Ï∂îÎ°†ÎêòÏßÄ ÏïäÍ≥† Í∞ôÏù¥ Ïã§Ï†ú Î¨∏ÏûêÏó¥ Í∞íÏúºÎ°ú Ï∂îÎ°† ÎêòÎèÑÎ°ù 
export const SETEXTRACELL = 'game/SETEXTRACELL' as const;
export const GAMERESET = 'game/GAMERESET' as const;
export const RECORDTIME = 'game/RECORDTIME' as const;

export interface GameState {
  gameRestart: boolean,
  isGameOver: number,
  takenTime:number
};

interface GameAction {
  type: string,
  payload: number,
  reset:boolean
}

const initialState: GameState = {
  gameRestart: false,
  isGameOver: 987654321,
  takenTime: -1
};

// action creator
export const setExtraCell = (diff: number) => ({
  type: SETEXTRACELL,
  payload: diff
});

export const gameReset = (diff: boolean) => ({
  type: GAMERESET,
  reset: diff
})

export const setRecordTime = (time:number) => ({
  type: RECORDTIME,
  payload: time
})

// Î¶¨ÎìÄÏÑú
export default function gameReducer(state: GameState = initialState, action: GameAction): GameState {
  switch (action.type) {
    case SETEXTRACELL:
      return {
        ...state,
        isGameOver: action.payload
      }
    case GAMERESET:
      return{
        ...state,
        gameRestart: action.reset
      }
    case RECORDTIME:
      return{
        ...state,
        takenTime: action.payload
      }
    default:
      return state;
  }
}